// ------------------    -------------------


//    header    
const headerHeight = document.getElementById('headerId');
const headerButton = document.getElementById('buttonId');
const headerAvatar = document.getElementById('avatarId');
headerHeight.classList.remove('_header-min');
headerButton.classList.remove('_button-min');
headerAvatar.classList.remove('_avatar-min');
const minY = 200;
window.onscroll = function () {
  //     Y
  const pageY = () => (window.pageYOffset || window.scrollY);
  let scrollYPos = pageY();
  // setTimeout(() => {

  // if   minY,    header, else  
  if (scrollYPos >= minY) {
    headerHeight.classList.add('_header-min');
    headerButton.classList.add('_button-min');
    headerAvatar.classList.add('_avatar-min');
  } else {
    headerHeight.classList.remove('_header-min');
    headerButton.classList.remove('_button-min');
    headerAvatar.classList.remove('_avatar-min');
  };
  // }, 200); //  transition  CSS
};

//       
document.addEventListener("DOMContentLoaded", function () {
  //        
  const scrollbar = Math.abs(document.body.clientWidth - window.innerWidth) + 'px';
  console.log(scrollbar);

  const pageOffset = () => (window.pageYOffset || window.scrollY);
  let prevBodyOverflow = document.body.style.overflow || 'initial'; //   overflow   
  let prevScrollYPosition = 0; //      scrollY

  //     
  function openModal(selector) {
    prevScrollYPosition = pageOffset(); //    scrollY
    prevBodyOverflow = document.body.style.overflow; //   overflow   
    const el = document.getElementById(selector);
    el.classList.add('_opened'); //   _opened
    document.body.style.overflow = 'hidden'; //   
    document.body.style.marginRight = scrollbar; //     (    )
    btnOpenElem.classList.remove('_visible');
  }
  //     
  function closeModal(selector) {
    const el = document.getElementById(selector);
    el.classList.remove('_opened'); //   _opened
    //    transition  CSS,    
    setTimeout(() => {
      document.body.style.overflow = prevBodyOverflow;
      document.body.style.marginRight = 0;
      btnOpenElem.classList.add('_visible');
    }, 200); //  transition  CSS
  }

  //     
  //     
  const modalTrigger = Array.from(document.querySelectorAll('[mobile-menu]')); //       mobile-menu
  console.log('modalTrigger = ' + modalTrigger); // ,   
  //        
  modalTrigger.forEach(element => {
    element.addEventListener('click', event => {
      const targetModalId = event.target.attributes['mobile-menu'].value;
      console.log('targetModalId = ' + targetModalId); //     
      openModal(targetModalId); //   ,   
    });
  });

  //     
  //     
  const modalCloseTrigger = Array.from(document.querySelectorAll('[mobile-menu-close]'));
  console.log(modalCloseTrigger);
  modalCloseTrigger.forEach(element => {
    element.addEventListener('click', event => {
      const targetModalId = event.target.attributes['mobile-menu-close'].value;
      console.log('targetModalId = ' + targetModalId);
      closeModal(targetModalId);
    });
  });


  // -------------      about  -----------------

  //       
  function openText(textSelector, buttonSelector) {

    const moreText = document.getElementById(textSelector);
    const btnText = document.getElementById(buttonSelector);

    console.log('btn "See more..." was pressed');

    btnText.classList.add('_hide-button');
    moreText.classList.remove('_hide-text');
    moreText.classList.add('_visible-text');
  }

  let btns = document.querySelectorAll('.about-item__see-more');

  for (i = 0; i < btns.length; i++) {
    let numBtn = 'btn-' + (i + 1);
    let numTxt = 'more-' + (i + 1);

    document.getElementById(numBtn).addEventListener('click', event => {
      console.log('btn "' + numBtn + '" was pressed');
      openText(numTxt, numBtn);
    });

  };

  //   1- 
  // document.getElementById("btn-01").addEventListener('click', event => {
  //   openText('more-01', 'btn-01');
  // });
  // //   2- 
  // document.getElementById("btn-02").addEventListener('click', event => {
  //   openText('more-02', 'btn-02');
  // });
  // //   2- 
  // document.getElementById("btn-03").addEventListener('click', event => {
  //   openText('more-03', 'btn-03');
  // });
});